            transform: scale(0.75);
    -webkit-filter: blur(2px);
            filter: blur(2px); }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    -webkit-filter: blur(0px);
            filter: blur(0px); } }

@keyframes App-logo-in {
