.App-menu-overlay.hidden {
  position: fixed;
  top: 50%;
  left: 50%;
  width: 100%;
  height: auto;
  z-index: 2000;
  visibility: hidden;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  -webkit-transform: translateX(-50%) translateY(-50%);
          transform: translateX(-50%) translateY(-50%); }
  .App-menu-overlay.hidden .App-menu-overlay-content {
    -webkit-transform: scale(0.8);
            transform: scale(0.8);
    opacity: 0;
    transition: all 0.3s;
    background: transparent; }
    .App-menu-overlay.hidden .App-menu-overlay-content > ul {
      text-align: center;
      list-style: none;
      padding: 0;
      margin: 0; }

.App-menu-overlay.show {
  position: fixed;
  top: 50%;
  left: 50%;
  width: 100%;
  height: auto;
  z-index: 2000;
  visibility: visible;
  -webkit-backface-visibility: visible;
          backface-visibility: visible;
  -webkit-transform: translateX(-50%) translateY(-50%);
          transform: translateX(-50%) translateY(-50%); }
  .App-menu-overlay.show .App-menu-overlay-content {
    display: block;
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1;
    transition: all 0.3s;
    background: transparent; }
    .App-menu-overlay.show .App-menu-overlay-content > ul {
      text-align: center;
      list-style: none;
      padding: 0;
      margin: 0; }

.App-menu-overlay-bg {
  position: fixed;
  width: 100%;
  height: 100%;
  visibility: hidden;
  top: 0;
  left: 0;
  z-index: 1000;
  opacity: 0;
  background: rgba(255, 255, 255, 0.2);
  transition: all 0.3s; }
  .App-menu-overlay-bg > .menu-close {
